.app {
  max-width: 1200px;
  border: 5px solid gray;
  margin: 20px auto;
  padding: 20px;
}

.products {
  margin: 20px 50px;
}

.products button {
  color: #fff;
  background-color: #333;
  border: none;
  border-radius: 3px;
  margin-top: 5px;
  padding: 10px 15px;
  font-size: 14px;
}

.products p {
  font-size: large;
}

.products h2 {
  color: #333;
  font-size: 45px;
}

.sortByPrice select {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.sort-by-price {
  margin-bottom: 10px;
}

.product {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px 10px 20px 100px;
  box-shadow: 0 2px 12px #0003;
}

.product h3 {
  color: #333;
  font-size: 24px;
}

.product p {
  color: #777;
  margin: 5px 0;
  font-size: large;
}

.product img {
  width: 350px;
  margin-bottom: 10px;
  box-shadow: 0 2px 20px #0003;
}

.product form {
  align-items: center;
  font-size: large;
  display: flex;
}

.product input[type="number"] {
  width: 110px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

.product button {
  color: #fff;
  background-color: #333;
  border: none;
  border-radius: 3px;
  margin-top: 5px;
  padding: 10px 15px;
  font-size: 14px;
}

.product p.stock {
  color: #333;
  font-weight: bold;
}

.outOfStock {
  color: red;
}

.menu {
  justify-content: flex-end;
  margin-bottom: 10px;
  display: flex;
}

.menu button {
  color: #fff;
  background-color: #999;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 20px 30px;
  transition: background-color .3s;
}

.menu button:hover {
  background-color: #777;
}

